@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Roboto-CondensedBold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Roboto-CondensedRegular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 800;
  src: url("../fonts/AlumniSans-ExtraBold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/AlumniSans-SemiBold.woff2") format("woff2");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.header {
  position: absolute;
  width: 100%;
  z-index: 1;
}

.header__wrapper {
  margin: 0 auto;
  padding: 30px 103px;
  max-width: 1366px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1199px) {
  .header__wrapper {
    padding: 22px 45px;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    padding: 13px 15px;
  }
}

@media (max-width: 1199px) {
  .header__logo-link {
    padding: 10px 0;
  }
}
@media (max-width: 767px) {
  .header__logo-link {
    padding: 0;
  }
}

.header__phone-link {
  padding: 0;
  margin: 0;
  grid-column: 2/3;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 32px;
  line-height: 26px;
  font-style: normal;
  font-weight: 600;
  color: #ffffff;
  letter-spacing: 0.03rem;
}
.header__phone-link:hover, .header__phone-link:focus {
  color: #103aae;
  outline: none;
}
.header__phone-link:active {
  color: #102564;
  border-bottom: 1px solid #102564;
}
.header__phone-link:disabled {
  color: rgba(255, 255, 255, 0.5019607843);
}
@media (max-width: 1199px) {
  .header__phone-link {
    font-size: 28px;
    letter-spacing: 0.06rem;
  }
}
@media (max-width: 767px) {
  .header__phone-link {
    font-size: 26px;
  }
}

.hero {
  background: conic-gradient(from 90deg at 16.03% 39.06%, #b80027, #003d85 62deg, #b80027 335deg);
}

.hero__wrapper {
  margin: 0 auto;
  padding: 87px 308px 118px 103px;
  display: grid;
  grid-template-columns: 1fr 500px;
  gap: 50px 70px;
  max-width: 1366px;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .hero__wrapper {
    padding: 84px 36px 100px;
    display: grid;
    width: 750px;
    grid-template-columns: 1fr 1fr;
    gap: 50px 30px;
  }
}
@media (max-width: 767px) {
  .hero__wrapper {
    padding: 61px 0 70px;
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 0;
  }
}

.hero__info {
  margin: 0;
  padding: 57px 0 0;
  display: grid;
  grid-template-columns: 113px 270px;
  text-align: right;
}
@media (max-width: 1199px) {
  .hero__info {
    padding: 20px 0 0;
    grid-template-columns: 129px 187px;
    column-gap: 8px;
  }
}
@media (max-width: 767px) {
  .hero__info {
    margin: 0 auto;
    padding: 0 0 38px;
    grid-template-columns: 118px 183px;
    column-gap: 0;
  }
}

.hero__info p {
  margin: 0;
  padding: 15px 0 0;
  text-transform: uppercase;
  grid-column: 2/3;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 48px;
  font-style: normal;
  font-weight: 600;
  line-height: 30px;
  letter-spacing: 1.62px;
}
@media (max-width: 1199px) {
  .hero__info p {
    padding: 13px 0 0;
    font-size: 42px;
    font-style: normal;
    font-weight: 600;
    line-height: 42px; /* 100% */
    letter-spacing: 1.26px;
  }
}
@media (max-width: 767px) {
  .hero__info p {
    padding: 24px 5px 8px 0;
    font-size: 28px;
    letter-spacing: 2.96px;
  }
}

.hero__info h1 {
  margin: -37px 0 0 0;
  padding: 0;
  align-self: flex-end;
  grid-column: 2/3;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 92px;
  font-style: normal;
  font-weight: 800;
  line-height: 110px; /* 119.565% */
  letter-spacing: 4.6px;
  text-transform: uppercase;
}
@media (max-width: 1199px) {
  .hero__info h1 {
    margin-bottom: -15px;
    font-size: 72px;
    font-style: normal;
    font-weight: 800;
    line-height: 85px; /* 118.056% */
    letter-spacing: 0;
  }
}
@media (max-width: 767px) {
  .hero__info h1 {
    margin: 0;
    padding: 0 5px 22px 0;
    font-size: 62px;
    font-style: normal;
    font-weight: 800;
    line-height: 70px; /* 112.903% */
    letter-spacing: 1.86px;
  }
}

.hero__list {
  margin: 0;
  padding: 45px 0 0;
  grid-column: 1/2;
  grid-row: 2/3;
  list-style-type: none;
  align-self: center;
  text-align: right;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
}
@media (max-width: 1199px) {
  .hero__list {
    padding: 65px 17px 0 0;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px; /* 157.143% */
  }
}
@media (max-width: 767px) {
  .hero__list {
    padding: 53px 0 0;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 142.857% */
  }
}

.hero__btn {
  padding: 27px 55px;
  justify-self: flex-end;
  align-self: flex-start;
  color: #ffffff;
  background-color: transparent;
  border: 2px solid #ffffff;
  grid-column: 1/2;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 12px; /* 100% */
  letter-spacing: 0.6px;
  text-transform: uppercase;
}
.hero__btn:hover, .hero__btn:focus {
  color: #103aae;
  background-color: #ffffff;
  outline: none;
}
.hero__btn:active {
  color: #102564;
}
.hero__btn:disabled {
  color: rgba(255, 255, 255, 0.5019607843);
  border: 2px solid rgba(255, 255, 255, 0.5019607843);
}
@media (max-width: 1199px) {
  .hero__btn {
    margin: 15px 0 0;
    padding: 21px 32px;
    width: auto;
  }
}
@media (max-width: 767px) {
  .hero__btn {
    margin: 0 15px 0;
    padding: 20px 20px;
    width: calc(100% - 30px);
    text-align: center;
  }
}

.hero picture {
  grid-row: 1/3;
  grid-column: 2/3;
}

.hero img {
  object-fit: cover;
}
