.about-gym__video-btn:disabled {
  background: none url("../img/svg/video-play-disabled.svg") no-repeat;
}
@media (max-width: 1199px) {
  .about-gym__video-btn {
    width: 72px;
    height: 72px;
    background: none url("../img/svg/video-play-tablet.svg") no-repeat;
  }
}
@media (max-width: 1199px) and (hover: hover) {
  .about-gym__video-btn:hover:not(.focus-visible) {
    background: none url("../img/svg/video-play-hover-focus-tablet.svg") no-repeat;
  }
}
@media (max-width: 1199px) {
  .about-gym__video-btn.focus-visible:focus {
    background: none url("../img/svg/video-play-hover-focus-tablet.svg") no-repeat;
  }
}
@media (max-width: 1199px) {
  .about-gym__video-btn.focus-visible:active {
    background: none url("../img/svg/video-play-active-tablet.svg") no-repeat;
  }
}
