.footer {
  color: #ffffff;
  background-color: #030a1a;
}

.footer__wrapper {
  margin: 0 auto;
  padding-top: 41px;
  padding-bottom: 40px;
  max-width: 1366px;
  width: 100%;
  color: #ffffff;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1199px) {
  .footer__wrapper {
    padding-top: 46px;
    padding-bottom: 46px;
  }
}
@media (max-width: 767px) {
  .footer__wrapper {
    padding-top: 30px;
    padding-bottom: 45px;
    display: block;
  }
}

@media (max-width: 767px) {
  .footer__logo-link img {
    display: block;
    margin: 0 0 26px;
  }
}

.footer__navigation-list {
  margin: 0 12px 0 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  column-gap: 40px;
}
@media (max-width: 1199px) {
  .footer__navigation-list {
    margin: 0 5px 0 0;
    padding: 0;
    column-gap: 30px;
  }
}
@media (max-width: 767px) {
  .footer__navigation-list {
    margin: 0 0 54px;
    padding: 0;
    column-gap: 35px;
    row-gap: 16px;
  }
}

.footer__navigation-link {
  margin: 0;
  padding: 0;
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px; /* 157.143% */
  position: relative;
}
.footer__navigation-link:hover::after {
  position: absolute;
  content: "";
  left: 0;
  bottom: -5px;
  width: 100%;
  height: 1px;
  background-color: #ffffff;
}
.footer__navigation-link:focus-visible {
  outline: 1px solid #ffffff;
  outline-offset: 10px;
}
.footer__navigation-link:active {
  outline: none;
}
.footer__navigation-link:active::after {
  content: none;
}
.footer__navigation-link:disabled {
  color: rgba(255, 255, 255, 0.5019607843);
}

.footer__social-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}
@media (max-width: 767px) {
  .footer__social-list {
    justify-content: center;
  }
}

.footer__social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}
.footer__social-link:hover, .footer__social-link:focus-visible {
  background-color: #103aae;
  fill: #ffffff;
  outline: none;
}
.footer__social-link:active {
  background-color: #102564;
  fill: #ffffff;
  outline: none;
}
.footer__social-link:disabled {
  background-color: #ffffff;
  opacity: 0.3;
}

.footer__social-list svg {
  width: 14px;
  height: 14px;
}

